import React, { useEffect, useRef } from 'react'
import { init, dispose } from 'klinecharts'
import generatedKLineDataList from '../utils/generatedKLineDataList'
import Layout from '../Layout'

const types = [
  { key: 'candle_solid', text: 'Ëú°ÁÉõÂÆûÂøÉ' },
  { key: 'candle_stroke', text: 'Ëú°ÁÉõÁ©∫ÂøÉ' },
  { key: 'candle_up_stroke', text: 'Ëú°ÁÉõÊ∂®Á©∫ÂøÉ' },
  { key: 'candle_down_stroke', text: 'Ëú°ÁÉõË∑åÁ©∫ÂøÉ' },
  { key: 'ohlc', text: 'OHLC' },
  { key: 'area', text: 'Èù¢ÁßØÂõæ' }
]

export default function ChartTypeKLineChart () {
  const chart = useRef()

  useEffect(() => {
    chart.current = init('real-time-k-line')
    chart.current.applyNewData(generatedKLineDataList())
    return () => {
      dispose('real-time-k-line')
    }
  }, [])

  return (
    <Layout
      title="ÂõæË°®Á±ªÂûã">
      <div id="real-time-k-line" className="k-line-chart"/>
      <div
        className="k-line-chart-menu-container">
        {
          types.map(({ key, text }) => {
            return (
              <button
                key={key}
                onClick={_ => {
                  chart.current && chart.current.setStyleOptions({
                    candle: {
                      type: key
                    }
                  })
                }}>
                {text}
              </button>
            )
          })
        }
      </div>
    </Layout>
  )
}
